<template>
  <article>
    <h1><code>&lt;veui-alert&gt;</code></h1>
    <section>
      <div></div>
      <veui-alert
        type="success"
        message=""
        closable/>
      <veui-alert
        class="limit-width"
        type="success"
        message=""
        closable
        close-label=""/>
      <veui-alert
        type="warning"
        message=""/>
      <veui-alert
        class="limit-width"
        type="warning"
        :message="messages"
        closable/>
      <veui-alert
        type="info"
        message=""/>
      <veui-alert
        class="limit-width"
        type="info"
        message=""
        close-label=""/>
      <veui-alert
        type="error"
        message=""/>
      <veui-alert
        class="limit-width"
        type="error"
        message=""/>
    </section>
  </article>
</template>

<script>
import bus from '../bus'
import { Alert, Icon } from 'veui'

export default {
  name: 'alert',
  components: {
    'veui-alert': Alert,
    'veui-icon': Icon
  },
  data () {
    return {
      open: false,
      messages: [
        '1111',
        '2',
        '3',
        '4',
        '5'
      ],
      messageIndex: 0
    }
  },
  methods: {
    close () {
      this.open = true
    }
  },
  mounted () {
    this.$children.forEach(child => {
      child.$on('click', () => {
        bus.$emit('log', child.$el.getAttribute('ui'))
      })
    })
  }
}
</script>

<style scoped>
section {
  margin: 30px;
}

.veui-alert {
  margin: 30px 0;
}

.veui-alert.limit-width {
  width: 350px;
}
</style>



