<template>
  <article>
    <h1><code>&lt;veui-checkbox&gt;</code></h1>
    <section>
      <veui-checkbox></veui-checkbox>
    </section>
    <section>
      <veui-checkbox v-model="current" true-value="" false-value=""></veui-checkbox> {{ current }}
    </section>
    <section>
      <veui-checkbox v-model="picked1"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox ui="small" v-model="picked2"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox v-model="picked3"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox ui="small" v-model="picked4"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox indeterminate v-model="picked5"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox ui="small" indeterminate v-model="picked6"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox disabled v-model="picked7"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox ui="small" disabled v-model="picked8"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox disabled v-model="picked9"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox ui="small" disabled v-model="picked10"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox disabled indeterminate v-model="picked11"></veui-checkbox>
    </section>
    <section>
      <veui-checkbox ui="small" disabled indeterminate v-model="picked12"></veui-checkbox>
    </section>
  </article>
</template>

<script>
import { Checkbox } from 'veui'

export default {
  name: 'checkbox-demo',
  components: {
    'veui-checkbox': Checkbox
  },
  data () {
    return {
      current: '',
      picked1: false,
      picked2: false,
      picked3: true,
      picked4: true,
      picked5: false,
      picked6: false,
      picked7: true,
      picked8: true,
      picked9: false,
      picked10: false,
      picked11: false,
      picked12: false
    }
  }
}
</script>

<style lang="less" scoped>
section {
  margin-bottom: 1em;
}
</style>



